body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  min-height: 100%;
  width: 100%;
  display: flex;
  /* background-color: #c73a3a; */
  background-position: center;
  background-size: cover;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

* {
  padding: 0;
  margin: 0;
}
input {
  outline: none;
}
html,
body {
  height: 100%;
}
body {
  font-family: "Roboto", sans-serif;
  background: linear-gradient(
      45deg,
      rgba(4, 2, 96, 0.7),
      rgba(180, 49, 183, 0.9)
    ),
    linear-gradient(90deg, rgba(51, 136, 140, 0.3), rgba(87, 240, 240, 0.1));
  overflow: hidden;
}
.panel {
  width: 400px;
  height: 500px;
  border-radius: 10px;
  box-shadow: 0 0 10px black;
  background: #141519;
  margin: 0 auto;
  text-align: center;
  
}
.login_main{
  display: flex;
  justify-content: center;
  align-items: center;
}
.panel .login_content {
  margin-top: 5px;
  width: 100%;
  height: 155px;
  color: white;
  font-size: 20px;
}
.panel .state i.fa-ban {
  font-size: 40px;
}
.panel .state i.fa-unlock-alt {
  font-size: 25px;
  color: white;
  line-height: 33px;
  height: 30px;
  width: 30px;
  display: inline-block;
  border-radius: 50%;
  border: 2px solid;
}
.panel .state h2 {
  font-weight: 400;
}
.panel .form {
  width: 340px;
  margin: 5px auto;
}
.panel .btn_login {
  height: 45px;
  width: 90%;
  background-color: #8bc34a;
  border-radius: 45px;
  position: relative;
  line-height: 45px;
  text-align: center;
  font-weight: bold;
  color: white;
  margin-top: 10px;
  transition: background 0.2s;
  cursor: pointer;
  margin: auto;
}
.panel .login:active {
  transform: translateY(2px);
}
.panel .login:hover {
  background-color: #599b2d;
}
.panel .login:after {
  content: "\f084";
  font-family: "FontAwesome";
  position: absolute;
  width: 45px;
  height: 45px;
  background-color: #599b2d;
  color: #fff;
  text-shadow: 1px -1px #467a23, 2px -2px #487d24, 3px -3px #4a8025,
    4px -4px #4b8326, 5px -5px #4d8627, 6px -6px #4f8928, 7px -7px #508c28,
    8px -8px #528f29, 9px -9px #54922a, 10px -10px #55952b, 11px -11px #57982c;
  left: 0;
  top: 0;
  transform: rotate(90deg);
  border-radius: 50%;
  text-align: center;
  line-height: 45px;
}
.panel input[type="text"] ,.panel input[type="password"]{
  background-color: #22232a;
  border-radius: 5px;
  font-size: 15px;
  height: 45px;
  border: none;
  padding-left: 15px;
  width: calc(100% - 30px);
  margin-bottom: 10px;
}
.panel input[type="text"][placeholder],.panel input[type="password"][placeholder] {
  color: #656d79;
  font-size: 15px;
  font-weight: 500;
  width: calc(100% - 30px);
}
.panel .fack {
  margin-top: 30px;
  font-size: 14px;
}
.panel .fack i.fa {
  text-decoration: none;
  color: #fff;
  vertical-align: middle;
  font-size: 20px;
  margin-right: 5px;
}
.panel .fack a:link {
  color: #616973;
}
.panel .fack a:visited {
  color: #555c65;
}

